import React from "react";

const Person = (props) => {

    const id = props.person.id

    return (
        <div>
            Name: {props.person.name}
            &nbsp;	&nbsp;	&nbsp;Number: {props.person.number}
            <button onClick={() => props.removePerson({ id })}>Remove</button>
        </div>
    )
}
//<button onClick={props.handleClick}>Remove</button>
const Persons = ({ persons, filter, removePerson }) => {
    let personsToShow
    //console.log(filter)
    if (filter !== "") {
        personsToShow = persons.filter(person => person.name.toLowerCase().includes(filter.toLowerCase()))
    } else {
        personsToShow = persons
    }

    const rows = () => personsToShow.map(person =>
        <Person
            key={person.name}
            person={person}
            removePerson={removePerson}
        />

    )

    return (
        <ul>
            {rows()}
        </ul>
    )



}

export default Persons